:root {
  font-family: "Commissioner", sans-serif;
}

:root {
  --moderate-cyan: hsl(176, 50%, 47%);
  --dark-cyan: hsl(176, 72%, 28%);
  --black: hsl(0, 0%, 0%);
  --dark-gray: hsl(0, 0%, 48%);
  --light-gray: hsl(0, 0%, 98%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

body {
  background-color: var(--light-gray);
}

.container {
  padding: 0 1.5rem;
  margin: 0 auto;
}
@media (min-width: 40em) {
  .container {
    padding: 3rem;
  }
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    width: 77%;
    max-width: 69.375rem;
  }
}

.header {
  min-height: 18.75rem;
  background: url(./../../../images/image-hero-mobile.jpg) no-repeat;
  background-size: cover;
}
.header__logo {
  z-index: 3;
  cursor: pointer;
}
.header__gradient {
  width: 100%;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0.0001) 100%);
  mix-blend-mode: normal;
  height: 40%;
}
.header .navbar {
  z-index: 2;
  padding-top: 2rem;
  padding-bottom: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .nav {
  position: absolute;
  right: 0;
  top: -100%;
  width: 100%;
  height: 100%;
  z-index: 2;
  padding: 0 1.5rem;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0.0001) 100%);
  mix-blend-mode: normal;
  transition: all ease-in-out 0.3s;
}
.header .nav.open {
  top: 0;
}
.header .nav__list {
  list-style: none;
  width: 100%;
  margin-top: 5.5rem;
  background: white;
  padding: 0;
  border-radius: 0.5rem;
}
.header .nav__list .navlink:not(:last-child) {
  border-bottom: 1px solid rgba(47, 47, 47, 0.1);
}
.header .nav__list .navlink a {
  text-decoration: none;
  color: #000;
  display: inline-block;
  width: 100%;
  padding: 1.5rem;
  font-style: normal;
  font-weight: 500;
  font-size: 1.125rem;
  line-height: 1.2;
}
.header__menuControls {
  z-index: 3;
  cursor: pointer;
}
.header__menuControls .closeMenu {
  display: none;
}
.header__menuControls .burger {
  display: inline-block;
}
.header__menuControls.open .closeMenu {
  display: inline-block;
}
.header__menuControls.open .burger {
  display: none;
}
@media (min-width: 40em) {
  .header {
    background: url(./../../../images/image-hero-desktop.jpg) no-repeat;
    min-height: 25rem;
  }
  .header__menuControls {
    display: none;
  }
  .header .navbar {
    padding-top: 3rem;
    padding-bottom: 3rem;
  }
  .header .nav {
    position: static;
    padding: 0;
    background: none;
    width: initial;
  }
  .header .nav__list {
    width: initial;
    margin: 0;
    background: none;
    border-radius: 0;
    display: flex;
    align-items: center;
    gap: 2rem;
  }
  .header .nav__list .navlink {
    border: none;
  }
  .header .nav__list .navlink a {
    color: white;
    font-size: 0.8125rem;
    line-height: 1rem;
    padding: 0.1875rem;
  }
  .header .nav__list .navlink a:hover, .header .nav__list .navlink a:focus {
    text-decoration: underline;
  }
}